@import "../../../../variable.scss";

div.RESET_PASSWORD_CONTAINER {
    padding: 0;
    background-color: rgba(93, 114, 118, 1);
    background: radial-gradient(ellipse at center, rgba(93, 114, 118, 1) 0%, rgba(13, 44, 51, 1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#5d7276', endColorstr='#0d2c33', GradientType=1);

    min-height: 100vh;
    backdrop-filter: blur(70px);

    div.row {
        padding: 0;
        margin: 0;

        div.COVER_TITLE {
            padding: 0;
            width: 100%;

            img {
                height: 100%;
                width: 100%;
            }
        }

        div.MOBILE_TITLE {

            display: flex;
            align-items: center;
            justify-content: center;
            padding: 20px;

            img {
                width: 220px;
            }
        }

        div.LOGIN_FORM {

            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            img {
                width: 220px;
                margin-bottom: 60px;
            }

            h5 {
                margin: 0;
                font-family: Roboto;
                color: white;
                font-weight: 300;
                letter-spacing: .5px;
                font-stretch: wider;
            }

            span {
                font-family: Roboto;
                color: white;
                opacity: .6;
                font-size: 13px;
                text-align: center;
                letter-spacing: .4px;
                margin: 15px;
            }

            form {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-direction: column;

                span {
                    font-family: Roboto;
                    font-size: 11px;
                    text-align: left;
                    align-self: flex-start;
                    color: #e26868;
                    margin: 0;
                    letter-spacing: 1px;
                }

                div.COMMON_INPUT {
                    margin-top: 30px;
                }

                button {
                    width: 100%;
                    height: 43px;
                    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
                    background-color: $PRIMARY_COLOR;
                    border: none;
                    border-radius: 3px;
                    color: white;
                    font-size: 12px;
                    font-family: Roboto;
                    letter-spacing: 1px;
                    margin-top: 60px;
                    outline: none;
                    a{
                        color: white;
                        text-decoration: none;
                    }
                }

                a {
                    text-decoration: underline;
                    font-family: Roboto;
                    color: $PRIMARY_COLOR;
                    font-size: 13px;
                    margin-top: 40px;
                    margin-bottom: 30px;
                }
            }
        }
    }
}

@media (max-width:768px) {
    div.COVER_TITLE {}
}